 *
 * @author fqq
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

import Url from "../../../api";

@Component({
  components: {}
})
export default class AddInterface extends Vue {
  @Prop({ type: String, default: "添加接口" })
  dialogTitle?: string;

  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;

  @Prop({ type: Object, default: () => ({}) })
  data!: any;

  // @Prop({ type: Array })
  // sysOptions!: any;

  @Watch("dialogVisible")
  handleVisible(newValue: boolean) {
    this.addDialog = newValue;
    if (newValue) {
      this.$nextTick(() => {
        let ref: any = this.$refs.addForm;
        ref.resetFields();
        this.addForm = JSON.parse(JSON.stringify(this.data));
      });
    }
  }

  addDialog = false;
  addForm: any = {
    id: "",
    name: "",
    uri: "",
    remark: ""
  };
  formRules: any = {
    name: [{ required: true, trigger: "blur", message: "请输入名称" }],
    uri: [{ required: true, trigger: "blur", message: "请输入接口" }]
  };
  loading = false;

  closeDialog() {
    this.$emit("close");
  }

  saveSensorAttribute() {
    let rest: any = this.$refs.addForm;
    rest.validate((valid: any) => {
      if (valid) {
        this.loading = true;
        let url = this.addForm.id ? Url.editInterface : Url.addInterface;
        if (typeof this.addForm.remark === "string") {
          this.addForm.remark = this.addForm.remark.trim();
        }
        this.$http
          .post(url, this.addForm)
          .then((res: any) => {
            this.loading = false;
            if (res.data.status) {
              this.$message.success(this.addForm.id ? "编辑成功" : "添加成功");
              this.$emit("submit");
            } else {
              this.$message.error(res.data.message);
            }
          })
          .catch(() => {
            this.loading = false;
          });
      } else {
        return false;
      }
    });
  }
}
